
import React,{useEffect} from 'react'
import { connect } from "react-redux";
import { fetchProducts } from "../actions/productActions";
import { postCart } from "../actions/CartAction";
import AddProducts  from "./productDetails";
import {Link} from 'react-router-dom'
import '../components/styles/products.css'

 function Products(props) {
   
  // let addCart = (id)=>{
  //  console.log();
  //   props.postCart(id)
  // }
  console.log(props.cartSuccess);
  useEffect(() => {   
  props.fetchProducts()
 
},[]); 
    return (
        <div class="container mt-5 mb-5">
   
    <div class="row g-2">
        
        <div class="col-md-12">

  

 <div className="row" id="product">

{

        props.products ?
  props.products.map((item)=> {
    return(
      <div className="col-md-4" key={item._id}>
       <div className="productScreen" >
      <div className="card rounded h-100">
      <Link to={'/productDetails/'+ item._id} className="card-image">
              {/* <span className="card-notify-badge">{product.name}</span> */}
              <span className="card-notify-year">-20%</span>
              <img className="img-fluid" src={item.image} alt="" />
              </Link>

          <div className="card-body">
              <h5 className="card-title">{item.name}</h5>
              <p className="card-text text-center">{item.price}$</p>
    
              <button onClick={()=> props.postCart(item._id)
             } class="addCart">add to cart</button>
        </div>
          </div>
          </div>
      
      
   </div>
    )
  }

    ): "null"
}

</div>




            </div>
            </div>
            </div>
          ) }
export default connect((state) => 
	({ products: state.products.filteredItems,cartSuccess:state.cart.cart }),
  {
    fetchProducts,
    postCart
    
  
  }
)(Products);