
import React,{useEffect, useState} from 'react'
import '../components/styles/css/easion.css'
import axios from 'axios'
 function AdminDash(props) {
  const [getSEllerInfo,setgetSEllerInfo] = useState([]);

  const click = async (data) =>{
   
    let value = {
      status:"true",
      email: data
    }
    console.log('there are:',value)
     const res = await axios.post('http://127.0.0.1:3000/api/admins/updateStatusSeller',value)
     .then((res)=>{
      console.log(res.data);

     })
      .catch((error)=>{
        console.log(error);
      })
   
    
   }

        


        useEffect(()=>{
          getsellers()
        },[])
   
         const getsellers = async () =>{

          const res = await axios.get('http://127.0.0.1:3000/api/admins/getAllSellers')
        //   console.log(JSON.parse(res.data.user));
          
        .then( res =>{
          setgetSEllerInfo(res.data.docs)
           console.log(res.data.docs)
        })
        .catch(err =>{
          console.log(err.data)


        })

        
        
            
        }
         

    return (
        
      
      <div className="dash">
        <div className="dash-nav dash-nav-dark">
            <header>
                <a href="#!" className="menu-toggle">
                    <i className="fas fa-bars"></i>
                </a>
                <a href="index.html" className="easion-logo"><i className="fas fa-sun"></i> <span>Easion</span></a>
            </header>
            <nav className="dash-nav-list">
                <a href="/" className="dash-nav-item">
                    <i className="fas fa-home"></i> Dashboard </a>
             
                    <a href="#!" className="dash-nav-item">
                        <i className="fas fa-file"></i> Layouts </a>
                    
                
            </nav>
        </div>
        
        <div className="dash-app">
           
           <table className="table">
  <thead className="thead-dark">
    <tr>
      <th scope="col">email</th>
      <th scope="col">actions</th>
    </tr>
  </thead> 
  <tbody>
   {
     
     console.log(getSEllerInfo)
   }

{
    getSEllerInfo ?
    getSEllerInfo.map(item =>(
    <tr key={item._id}>
      <td>{item.email}</td>
       <td> 
      <button onClick={()=>click(item.email)} className="btn btn-success  mx-auto">Approved</button>
      </td>
    </tr>





)

):"null" } 









    
  </tbody>
</table>
    </div>
    </div>
)
    }
export default AdminDash