import { FETCH_PRODUCTS,ADD_PRODUCT,SELLERADDPRO_ERROR,FECH_SEELER_PRO_ERROR,
  FECH_SEELER_PRO,
  PRODUCT_INFO,
  PRODUCT_INFO_ERROR,
  GETTRUE
 } from "../types";
import axios from 'axios'
const access_token =  localStorage.getItem('jwt_seller');

export const fetchProducts = () => async (dispatch) => {
  const res =  await axios.get('https://sbay3.cleverapps.io/api/product')
  const data = res.data
  // console.log(data.products);
  dispatch({
    type: FETCH_PRODUCTS,
    payload: data.products,
  });
};

export const fetchSellerProducts = () => async (dispatch) => {
  const res =  await axios.get('https://sbay3.cleverapps.io/api/product/sellerProduct',{
    headers: {
      'auth-token': access_token
    }
    
    }).then(res =>{
      dispatch({ type: FECH_SEELER_PRO, payload: res });
      console.log("products" + JSON.stringify(res));

   })        
   .catch(error =>{
     console.log("here aoub " + error);
     dispatch({ type: FECH_SEELER_PRO_ERROR, payload: error});

   }) // console.log(data.products);

};


export const addproduct =  (data) =>  async (dispatch) => {



   const res =  await axios.post('https://sbay3.cleverapps.io/api/product/addProduct',data,{
    headers: {
      'auth-token': access_token
    }
    
    }).then(res =>{
      dispatch({ type: ADD_PRODUCT, payload: res.data });

   })        
   .catch(error =>{
     // console.log("here" + error.response.data);
     dispatch({ type: SELLERADDPRO_ERROR, payload: error.response.data});

   })
       
        

    
};







export const productInfo =  (data) =>  async (dispatch) => {



  const res =  await axios.post('https://sbay3.cleverapps.io/api/product/ProductDetails',data)
  .then(res =>{
     dispatch({ type: PRODUCT_INFO, payload: res });

  })        
  .catch(error =>{
    // console.log("here" + error.response.data);
    dispatch({ type: PRODUCT_INFO_ERROR, payload: error});

  })
      
       

   
};
